.App {
  text-align: center;
}

.ColorPicker {
  margin: 20px;
  text-align: center;
}

.ColorPicker button {
  background-color: #0074d9;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

.ColorList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Color {
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}